import React, { useRef } from 'react';

import {
    AlertDialog,
    AlertDialogBody,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogContent,
    AlertDialogOverlay,
    Button,
    useDisclosure,
    useToast,
} from '@chakra-ui/react'

import { LockIcon } from '@chakra-ui/icons';

// contexts
import { useAuth } from 'shared/contexts/Auth';

// forms
import { useForms } from '../Forms/hooks/useForms';
import { ChangePasswordForm } from '../Forms';

// api 
import { changePassword } from 'shared/services/api';
import { handleServerError } from 'shared/services/handleServerError';


export function PasswordModal({
    buttonProps = {
        borderWidth: 1,
        variant: 'outline',
        borderColor: 'blue.300',
        w: 'full'
    },
    // size = 'xs',
}) {

    const toast = useToast();
    const { userInfo, } = useAuth();

    const {
        setLoading,
        forms,
        setErrorServer,
        validateForms,
        ...formProps
    } = useForms({ type: 'changePassword' });

    const { isOpen, onOpen, onClose } = useDisclosure()
    const cancelRef = useRef();

    const handleChangePassword = async () => {
        setErrorServer('');
        if (validateForms()) {

            setLoading(true);
            const dataAuth = {
                password: forms.lastPassword,
                newPassword: forms.password,
            };
            try {
                const res = await changePassword(dataAuth, userInfo?.token);

                if ('error' in res) {
                    setLoading(false);
                    handleServerError(res, setErrorServer);
                    return;
                };

                onClose();
                toast({
                    description: 'Password updated!',
                    status: 'success',
                    duration: 9000,
                    isClosable: true
                });
            } catch (e) {
                handleServerError(e, setErrorServer);
            };

            setLoading(false);
        };
    };

    return (
        <>
            <Button
                {...buttonProps}
                onClick={onOpen}
                leftIcon={<LockIcon />}
            >
                Change my password
            </Button>

            <AlertDialog
                // size={size}
                variant={'sandvpn'}
                motionPreset='slideInBottom'
                isOpen={isOpen}
                leastDestructiveRef={cancelRef}
                onClose={onClose}
                isCentered
            >
                <AlertDialogOverlay>
                    <AlertDialogContent>
                        <AlertDialogHeader fontSize='lg' fontWeight='bold'>
                            Change my password
                        </AlertDialogHeader>

                        <AlertDialogBody>
                            <ChangePasswordForm
                                {...formProps}
                                helperPasswordText={"At least 8 characters with 1 uppercase & lowercase letter, 1 number and 1 symbol"}
                            />

                            <Button onClick={handleChangePassword} w={'full'}>
                                Change my password
                            </Button>
                        </AlertDialogBody>

                        <AlertDialogFooter>
                            <Button ref={cancelRef} onClick={onClose}>
                                Cancel
                            </Button>
                        </AlertDialogFooter>
                    </AlertDialogContent>
                </AlertDialogOverlay>
            </AlertDialog>
        </>
    )
}