import React from "react";

// Chakra imports
import {
    Flex,
} from "@chakra-ui/react";


export default function StatusContainer({
    statusContainerProps = {
        h: '60px',
        w: '100%',
        justify: 'align',
    },
    children
}) {
    return (
        <Flex
            {...statusContainerProps}
        >
            {children}
        </Flex>
    );
};
