import React from 'react';

import {
    Flex,
    Text,
    Icon
} from "@chakra-ui/react";

import { useNavigate, useLocation } from 'react-router-dom';

// constants
import { routeConstants } from "shared/constants/route";


export default function BottomNavigation() {

    const location = useLocation();
    const navigate = useNavigate();

    return (

        <Flex
            pos={'fixed'}
            bottom={0}
            align={'center'}
            justify={'space-around'}
            w={'100vw'}
            bg={'blue.900'}
            h={'60px'}
            borderRadius={5}
        >

            {
                routeConstants.map((route, index) => (
                    <Flex
                        key={index}
                        flexDir={'column'}
                        justify={'center'}
                        align={'center'}
                        cursor={'pointer'}
                        onClick={() => navigate(route.pathname)}
                    >
                        <Icon
                            h={location.pathname === route.pathname ? 30 : 25}
                            w={location.pathname === route.pathname ? 30 : 25}
                            color={location.pathname === route.pathname ? 'red.200' : 'blue.200'}
                            as={route.icon}
                        />
                        <Text
                            fontSize={location.pathname === route.pathname ? 16 : 14}
                            color={location.pathname === route.pathname ? 'red.200' : 'blue.200'}
                            fontWeight={600}
                            userSelect={'none'}
                        >
                            {route.name}
                        </Text>
                    </Flex>
                ))
            }
        </Flex>
    );
};