import React from "react";

// Chakra imports
import {
    Flex,
    Spinner,
} from "@chakra-ui/react";


export default function LoadingContainer({
    loading,
    spinnerProps = {},
    loadingContainerProps = {
        flex: 1,
        justify: "center",
        align: 'center',
    },
}) {
    return (
        <Flex
            display={loading ? 'flex' : 'none'}
            {...loadingContainerProps}>
            {
                loading && (
                    <Spinner {...spinnerProps} />
                )
            }
        </Flex>
    );
};
