import React from "react";

import {
    FormContainer,
    AlertContainer,
    LoadingContainer,
    StatusContainer
} from "../container";

import { PasswordForm } from "../components";


export default function ChangePasswordForm({
    loading,
    errors,
    errorServer,
    setError,
    handleChange,
    helperPasswordText = 'At least 8 characters with 1 uppercase & lowercase letter, 1 number and 1 symbol',
    handleEnterPress = (() => null),
    formPassword = 'Password',
    formNewPassword = 'New Password',
    formPasswordConfirm = 'Confirm Password',
    placeholderPassword = 'Your password',
}) {

    const defaultPlaceholder = {
        fontSize: 'sm',
        borderRadius: '15px',
        size: 'lg',
        _placeholder: { color: 'gray.300' },
        _hover: { borderColor: 'blue.100' }
    };

    return (
        <FormContainer>
            <PasswordForm
                formLabel={formPassword}
                onChange={(e) => handleChange(e, 'lastPassword')}
                error={errors.lastPassword}
                onFocus={() => setError('lastPassword', false)}
                onEnterPress={handleEnterPress}
                mb='5px'
                inputProps={{ ...defaultPlaceholder, placeholder: placeholderPassword }}
            />

            <PasswordForm
                formLabel={formNewPassword}
                onChange={(e) => handleChange(e, 'password')}
                helperText={helperPasswordText}
                error={errors.password}
                onFocus={() => setError('password', false)}
                onEnterPress={handleEnterPress}
                mb='5px'
                inputProps={{ ...defaultPlaceholder, placeholder: placeholderPassword }}
            />

            <PasswordForm
                formLabel={formPasswordConfirm}
                onChange={(e) => handleChange(e, 'confirm_password')}
                error={errors.confirm_password}
                onFocus={() => setError('confirm_password', false)}
                onEnterPress={handleEnterPress}
                mb='5px'
                inputProps={{ ...defaultPlaceholder, placeholder: placeholderPassword }}
            />

            <StatusContainer>
                <AlertContainer
                    message={errors.message}
                    errorServer={errorServer}
                    loading={loading}
                />
                <LoadingContainer
                    loading={loading}
                />
            </StatusContainer>
        </FormContainer>
    );
};
