/*global browser*/
import React, { useMemo, useEffect, useState } from 'react';


const initialStateManag = {
    showAnonymousLoginModal: true,
    notification: {
        unread: [],
        all: [],
        read: [],
    },
};

export const ManagementContext = React.createContext();

export const ManagementProvider = (props) => {

    const [management, setManagement] = useState(initialStateManag);

    useEffect(() => {
        const getManagement = async () => {
            const management_ = (await browser.storage.sync.get("management"))?.management;
            management_ ?
                setManagement(management_) : browser.storage.sync.set({ management: initialStateManag });
        };

        getManagement();
    }, []);

    const defaultManagement = useMemo(() => ({
        management,
        setManagement,
    }), [management]);

    return (
        <ManagementContext.Provider value={defaultManagement}>
            {props.children}
        </ManagementContext.Provider>
    );
};

export const useManagement = () => {
    const context = React.useContext(ManagementContext);
    if (!context) {
        throw new Error('Error');
    }
    return context;
};