import * as React from 'react';
import {
    FormControl,
    FormLabel,
    FormHelperText,
    IconButton,
    Input,
    InputGroup,
    InputRightElement,
    useDisclosure,
    useMergeRefs,
    Icon,
    Box,
    Flex
} from '@chakra-ui/react';

// Icons
import { ViewIcon, ViewOffIcon } from '@chakra-ui/icons';

// utils
import { onEnterPress_ } from '../utils';


export const PasswordForm = React.forwardRef(({
    formLabelRight,
    formLabelRightProps,
    handleLabelRightClick,
    error,
    errorProps,
    noErrorProps,
    formLabel,
    helperText,
    formLabelProps,
    formHelperTextProps,
    formControlProps,
    inputGroupProps,
    eyeIconProps,
    inputProps,
    onEnterPress,
    ...props
}, ref) => {
    const { isOpen, onToggle } = useDisclosure();
    const inputRef = React.useRef(null);

    let isValidProps = error ? errorProps : noErrorProps;

    const mergeRef = useMergeRefs(inputRef, ref);
    const onClickReveal = () => {
        onToggle()
        if (inputRef.current) {
            inputRef.current.focus({ preventScroll: true })
        }
    };

    return (
        <FormControl {...formControlProps}>
            <Flex>
                <FormLabel htmlFor="password" {...formLabelProps}>{formLabel}</FormLabel>
                {
                    formLabelRight && (
                        <Box
                            ml={'auto'}
                            {...formLabelRightProps}
                            onClick={handleLabelRightClick}
                        >
                            {formLabelRight}
                        </Box>
                    )
                }
            </Flex>
            <InputGroup {...inputGroupProps}>
                <InputRightElement>
                    <IconButton
                        tabIndex={-1}
                        variant="link"
                        aria-label={isOpen ? 'Mask password' : 'Reveal password'}
                        icon={
                            <Icon
                                as={isOpen ? ViewIcon : ViewOffIcon}
                                {...eyeIconProps}
                            />
                        }
                        onClick={onClickReveal}
                    />
                </InputRightElement>
                <Input
                    ref={mergeRef}
                    name="password"
                    type={isOpen ? 'text' : 'password'}
                    autoComplete="current-password"
                    required
                    onKeyPress={(ev) => onEnterPress_(ev, onEnterPress)}
                    {...isValidProps}
                    {...inputProps}
                    {...props}
                />
            </InputGroup>
            {
                helperText && (
                    <FormHelperText {...formHelperTextProps}>
                        {helperText}
                    </FormHelperText>
                )
            }
        </FormControl>
    )
});

PasswordForm.displayName = 'PasswordForm';

PasswordForm.defaultProps = {
    formLabel: 'Password',
    formLabelRight: '',
    formLabelRightProps: { pr: '5px', fontSize: 14, fontWeight: 400, cursor: 'pointer' },
    helperText: '',
    formLabelProps: { fontSize: 'sm', fontWeight: 'normal', },
    formControlProps: { isRequired: true, },
    inputGroupProps: { size: 'lg', },
    eyeIconProps: {
        width: '25px',
        height: '25px',
        color: 'gray.200',
    },
    error: false,
    errorProps: { borderColor: 'red.200', borderWidth: '2px' },
    noErrorProps: { borderColor: 'blue.200', borderWidth: '1px' },
    inputProps: {
        fontSize: 'sm',
        borderRadius: '15px',
        placeholder: 'Your password',
        size: 'lg',
        _placeholder: { color: 'gray.300' },
        _hover: { borderColor: 'blue.200' },
        // _focus: { borderColor: 'blue.100' },
    },
    formHelperTextProps: {
        color: 'gray.300'
    },
    onEnterPress: () => null,
    handleLabelRightClick: () => null,
};