

import { useEffect } from 'react';
import {
    FormControl,
    FormLabel,
    Input,
    InputGroup,
    InputRightElement,
    chakra
} from '@chakra-ui/react';

import { CheckIcon, } from '@chakra-ui/icons';

// constant
import * as Regex from 'shared/constants/regex';

// hooks
import { useCustomState } from 'shared/hooks/useCustomState';


export function CheckEmail({
    handleEmailValid = () => null,
    handleEmailInvalid = () => null,
    email,
    setFocusError,
    focusError = false,
}) {

    const [form, setForm] = useCustomState({
        email: '',
        isEmailValid: false,
    });

    useEffect(() => {
        email.current = form.email;
        if (Regex.email.test(form.email)) {
            setForm({
                isEmailValid: true
            });
            handleEmailValid();
        }
        else {
            setForm({
                isEmailValid: false,
            });
            handleEmailInvalid();
        };
    }, [form.email]);

    const handleChange = (event) => setForm({ email: event.target.value.trim().toLowerCase() });

    return (
        <FormControl
            isInvalid={form.isEmailValid}
            w='full'
        >
            <FormLabel
                htmlFor="email"
                fontSize={16}
                m={0}
                p={'2.5px'}
            >
                <chakra.span color={'blue.50'}>
                    Email
                    <chakra.span color={'blue.50'} pl={'2px'} fontWeight={700}>
                        *
                    </chakra.span>
                </chakra.span>
            </FormLabel>
            <InputGroup>
                <Input
                    id="email"
                    type="email"
                    onChange={handleChange}
                    placeholder={'Your email address'}
                    spellCheck={false}
                    borderColor={focusError ? "blue.900" : "blue.100"}
                    borderWidth={2}
                    _hover={{ borderColor: "blue.200" }}
                    _placeholder={{ color: 'blue.50' }}
                    _focus={{ borderColor: "blue.200" }}
                    _invalid={{ borderColor: "green.200" }}
                    _valid={{ borderColor: "red.200" }}
                    color={'white'}
                    fontWeight={500}
                    size={'md'}
                    onFocus={() => setFocusError(false)}
                />
                <InputRightElement h="full">
                    {
                        form.isEmailValid && (<CheckIcon color='green.200' />)
                    }
                </InputRightElement>
            </InputGroup>
        </FormControl>
    );
};