import { fetchWithTimeout } from "./fetchWithTimeout";

export async function createCryptoCharge(UserData) {
    return fetch(process.env.REACT_APP_API_URL + "create_crypto_charge", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify(UserData)
    })
        .then(res => {
            if (!res.ok) {
                throw new Error('Bad fetch response')
            };
            return res.json();
        });
};

export async function changePassword(UserData, token) {
    return fetch(process.env.REACT_APP_API_URL + "change-password", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify(UserData)
    })
        .then(data => data.json())
};


