export async function login(UserData) {
    return fetch(process.env.REACT_APP_API_URL + "login", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(UserData)
    })
        .then(data => data.json())
        .catch((error) => {
            console.log(error)
        });
};

