import { useState, } from "react";
import { useCustomState, } from "./useCustomState";

// defaults
import {
    defaultForm,
    defaultReg,
    defaultMessageError,
    defaultError,
    defaultFormType
} from "./constants";


export const useForms = ({
    type,
    customForm,
    customError,
    custumRegex,
    customMessageError
}) => {

    const defaultErrors = defaultFormType.includes(type) ? defaultError[type] : customError;
    const defaultForms = defaultFormType.includes(type) ? defaultForm[type] : customForm;
    const defaultRegs = defaultFormType.includes(type) ? defaultReg : custumRegex;
    const defaultMessageErrors = defaultFormType.includes(type) ? defaultMessageError : customMessageError;

    const [forms, setForms] = useCustomState(defaultForms);
    const [errors, setErrors] = useCustomState(defaultErrors);


    const [loading, setLoading] = useState(false);
    const [errorServer, setErrorServer] = useState('');

    const handleChange = (event, key) => setForms({ [key]: event.target.value });

    const setError = (key, errorVal) => {
        if (errors[key] !== errorVal) {
            setErrors({ [key]: errorVal });
        };
    };

    const checkAllFilled = () => {
        let isAllFilledObj = {};
        let isAllFilled = true;

        for (const [formKey, formVal] of Object.entries(forms)) {
            isAllFilledObj[formKey] = !(!!formVal.trim());
            isAllFilled = isAllFilled && !!formVal.trim();
        };

        !isAllFilled && setErrors({
            ...isAllFilledObj,
            message: isAllFilled ? '' : defaultMessageErrors['allFilled']
        });

        return isAllFilled;
    };

    const checkMatchPwds = () => {
        const match = (forms.password.trim() === forms.confirm_password.trim());
        setErrors({
            ...defaultErrors,
            confirm_password: !match,
            message: !match ? defaultMessageErrors['match'] : ''
        });
        return match;
    };

    const validateRegex = (formKey) => {
        const isRegValidated = defaultRegs[formKey].test(forms[formKey].trim());
        setErrors({
            ...defaultErrors,
            [formKey]: !isRegValidated,
            message: !isRegValidated ? defaultMessageErrors[formKey] : ''
        });
        return isRegValidated;
    };

    const validateAllRegex = () => {
        let isAllRegValidated = true;

        for (const formKey of Object.keys(forms)) {
            isAllRegValidated = isAllRegValidated && validateRegex(formKey);
        };

        return isAllRegValidated;
    };

    const validateForms = () => {
        setErrorServer('');

        let isFormValidate = checkAllFilled();
        if (!isFormValidate) return false;
        isFormValidate = isFormValidate && validateAllRegex();
        if (!isFormValidate) return false;

        if (['register', 'forgot', 'changePassword'].includes(type)) {
            isFormValidate = isFormValidate && checkMatchPwds();
        };

        return isFormValidate;
    };

    return {
        loading,
        setLoading,
        forms,
        errors,
        setError,
        errorServer,
        setErrorServer,
        handleChange,
        validateForms,
    };
};