import React from "react";

import { Flex } from '@chakra-ui/react';

import {
    Routes,
    Route,
} from "react-router-dom";

// screens
import Connect from "App/screens/Connect";
import Locations from "App/screens/Locations";
import Settings from "App/screens/Settings/Settings";
import User from "App/screens/Profile/User";
import Payment from "App/screens/Payment/Payment";

// components
import BottomNavigation from "./BottomNavigation";


function Root() {
    return (
        <Flex
            h={'100vh'}
            w={'100vw'}
            flex={1}
            bgGradient='linear(blue.900 0%, blue.400 80%)'
        >
            <Flex
                h={'calc(100% - 60px)'}
                flex={1}
            >
                <Routes>
                    <Route path="/" element={<Connect />} />
                    <Route path="payment" element={<Payment />} />
                    <Route path="locations" element={<Locations />} />
                    <Route path="settings" element={<Settings />} />
                    <Route path="user" element={<User />} />
                </Routes>
            </Flex>
            <BottomNavigation />
        </Flex>
    );
}


export default Root;

