import React, { useRef } from 'react';

import {
    AlertDialog,
    AlertDialogBody,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogContent,
    AlertDialogOverlay,
    Button,
    useDisclosure,
    Tooltip,
} from '@chakra-ui/react'

import { DeleteIcon } from '@chakra-ui/icons';

export function DeleteDialog({
    size,
    onlyIcon = false,
    handleDelete,
    buttonTitle = 'Delete Account',
    iconProps,
    buttonProps = {
        borderWidth: 1,
        borderColor: 'red.200',
        backgroundColor: 'blue.600',
        w: 'full',
    },
    title = 'Delete Account',
    tooltipLabel = 'Delete credendial'
}) {
    const { isOpen, onOpen, onClose } = useDisclosure()
    const cancelRef = useRef();

    const handleConfirm = () => {
        handleDelete();
        onClose();
    };

    return (
        <>
            {
                onlyIcon ?
                    <Tooltip
                        bg={'blue.400'}
                        color={'white'}
                        hasArrow
                        placement='top'
                        label={tooltipLabel}>
                        <DeleteIcon
                            cursor={'pointer'}
                            onClick={onOpen}
                            {...iconProps}
                        />
                    </Tooltip>
                    :
                    <Button
                        {...buttonProps}
                        onClick={onOpen}
                        leftIcon={<DeleteIcon />}
                    >
                        {buttonTitle}
                    </Button>
            }

            <AlertDialog
                size={size}
                variant={'sandvpn'}
                motionPreset='slideInBottom'
                isOpen={isOpen}
                leastDestructiveRef={cancelRef}
                onClose={onClose}
                isCentered
            >
                <AlertDialogOverlay>
                    <AlertDialogContent>
                        <AlertDialogHeader fontSize='lg' fontWeight='bold'>
                            {title}
                        </AlertDialogHeader>

                        <AlertDialogBody>
                            Are you sure? You can&apos;t undo this action afterwards.
                        </AlertDialogBody>

                        <AlertDialogFooter>
                            <Button ref={cancelRef} onClick={onClose} _focus={{ boxShadow: "none", }}>
                                Cancel
                            </Button>
                            <Button colorScheme='red' onClick={handleConfirm} ml={3}>
                                Delete
                            </Button>
                        </AlertDialogFooter>
                    </AlertDialogContent>
                </AlertDialogOverlay>
            </AlertDialog>
        </>
    )
};