import {
    Modal,
    ModalOverlay,
    ModalContent,
    Spinner,
    ModalBody,
    Flex
} from '@chakra-ui/react';


export const LoadingModal = ({ isOpen }) => {
    return (
        <Modal
            isOpen={isOpen}
            isCentered
            variant='loading'
        >
            <ModalOverlay />
            <ModalContent>
                <ModalBody>
                    <Flex justify={'center'}>
                        <Spinner
                            color='blue.200'
                            w={'75px'}
                            h={'75px'}
                        />
                    </Flex>
                </ModalBody>

            </ModalContent>
        </Modal>
    )
};