export const handleServerError = async (
    e,
    setError,
    msg = 'Something went wrong, please try again later'
) => {
    if (
        ('response' in e) &&
        ('data' in e['response']) &&
        typeof e['response']['data'] === 'object' &&
        ('error' in e['response']['data'])
    ) {
        setError(e['response']['data']['error']);
    }
    else if (('error' in e)) {
        setError(e['error']);
    }
    else {
        setError(msg);
    };
};
