import React, { useRef } from 'react';

import {
    AlertDialog,
    AlertDialogBody,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogContent,
    AlertDialogOverlay,
    Button,
    useDisclosure,
    Image,
} from '@chakra-ui/react'

import { AndroidIcon } from "shared/components/Icons/Android";


export function AndroidModal({
    size = 'xs',
    title = 'Android App',
}) {
    const { isOpen, onOpen, onClose } = useDisclosure()
    const cancelRef = useRef();

    return (
        <>
            <AndroidIcon
                h={'35px'}
                w={'35px'}
                cursor={'pointer'}
                onClick={onOpen}
            />

            <AlertDialog
                size={size}
                variant={'sandvpn'}
                motionPreset='slideInBottom'
                isOpen={isOpen}
                leastDestructiveRef={cancelRef}
                onClose={onClose}
                isCentered
            >
                <AlertDialogOverlay>
                    <AlertDialogContent alignItems={'center'}>
                        <AlertDialogHeader fontSize='lg' fontWeight='bold'>
                            {title}
                        </AlertDialogHeader>

                        <AlertDialogBody>
                            <Image
                                w={'225px'}
                                h={'225px'}
                                src='/images/sandvpnQR.png'
                            />
                        </AlertDialogBody>

                        <AlertDialogFooter>
                            <Button ref={cancelRef} onClick={onClose} _focus={{ boxShadow: "none", }}>
                                Close
                            </Button>
                        </AlertDialogFooter>
                    </AlertDialogContent>
                </AlertDialogOverlay>
            </AlertDialog>
        </>
    )
};