import React from "react";

import {
    Flex,
    Box,
    Text,
    Heading,
    Button,
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    Divider,
    Tag,
    VStack,
    useToast,
    useDisclosure,
} from '@chakra-ui/react';

import { EmailIcon } from '@chakra-ui/icons';

import moment from 'moment';

// components 
import { DeleteDialog } from "shared/components/Dialog/Delete";
import { LoadingModal } from "shared/components/LoadingModal";
import { PasswordModal } from "shared/components/Dialog/PasswordModal";

// contexts
import { useAuth } from "shared/contexts/Auth";

// api
import { deleteAccount } from "shared/services/api";


const Profile = () => {

    const { userInfo, logout } = useAuth();
    const { isOpen, onOpen, onClose } = useDisclosure();
    const toast = useToast();

    const handleDeleteAccount = async () => {
        try {
            onOpen();
            await deleteAccount(userInfo['token']);
            logout();

            toast({
                description: 'Your account has been deleted',
                status: 'success',
                duration: 4000,
                isClosable: true
            });
        }
        catch (e) {
            onClose();
            toast({
                description: 'Something went wrong, Please contact us.',
                status: 'error',
                duration: 4000,
                isClosable: true
            });
        };
    };

    return (

        <Flex
            flex={1}
            align='center'
            justify={'center'}
        >

            <Card
                bg={'blue.600'}
                border={'1px solid'}
                borderColor="blue.300"
                shadow={'lg'}
                px={{ base: 5, md: 16, sm: 10 }}
                maxW={{ base: "sm", lg: "lg", md: "lg" }}
            >

                <Flex
                    alignItems="center"
                    p={1}
                    pos="absolute"
                    top={0}
                    right={0}
                >
                    <Tag
                        colorScheme={userInfo.premium ? 'orange' : 'red'}
                        fontWeight={600}
                        fontSize={16}
                    >
                        {userInfo.premium ? 'PREMIUM' : 'FREE'}
                    </Tag>
                </Flex>

                <CardHeader
                    align='center'
                    mt={3.5}
                    color={'white'}
                >
                    <Heading >My Account</Heading>
                </CardHeader>

                <CardBody
                    py={{ base: 5, md: 8 }}
                    px={{ base: 5, md: 10 }}
                >

                    <VStack spacing={3} alignItems="flex-start">

                        <Flex align={'center'}>
                            <Box w={45}>
                                <EmailIcon
                                    color={'blue.200'}
                                    w={'30px'}
                                    h={'30px'}
                                />
                            </Box>

                            <Box maxW={250}>
                                <Text color={'white'} fontSize={16}>
                                    {userInfo.email}
                                </Text>
                            </Box>

                        </Flex>

                        {
                            userInfo?.type && userInfo?.premium && (
                                <Flex align={'center'}>
                                    <Box w={45}>
                                        <Text color={'white'} fontWeight={600} fontSize={16}>
                                            Type:
                                        </Text>
                                    </Box>
                                    <Text
                                        color={'white'}
                                        fontSize={16}
                                    >
                                        {userInfo.type}
                                    </Text>
                                </Flex>
                            )
                        }

                        {
                            userInfo?.expiration && userInfo?.premium && (
                                <Flex align={'center'}>
                                    <Box w={'60px'}>
                                        <Text color={'white'} fontWeight={600} fontSize={16}>
                                            Expiry:
                                        </Text>
                                    </Box>
                                    <Text
                                        color={'white'}
                                        fontSize={14}
                                    >
                                        {moment(userInfo.expiration).format('ddd, DD MMM YYYY hh:mm A')}
                                    </Text>
                                </Flex>
                            )
                        }

                    </VStack>

                </CardBody>
                <Divider />
                <CardFooter justify='center'>
                    <VStack w={'full'}>
                        <DeleteDialog
                            size='xs'
                            handleDelete={handleDeleteAccount}
                        />

                        <PasswordModal />

                        <Button
                            w={'full'}
                            borderWidth={1}
                            borderColor={'blue.300'}
                            variant='outline'
                            onClick={logout}
                            color={'white'}
                            _hover={{
                                bg: 'blue.500'
                            }}
                        >
                            Logout
                        </Button>
                    </VStack>
                </CardFooter>
            </Card>

            <LoadingModal
                isOpen={isOpen}
            />

        </Flex>
    );
};

export default React.memo(Profile);