/*global browser*/
import React, { useState } from "react";

import {
    Box,
    Button,
    Container,
    FormControl,
    FormLabel,
    Heading,
    HStack,
    Input,
    Stack,
    Text,
    Flex,
} from '@chakra-ui/react';

// components
import { PasswordField } from "shared/components/PasswordField";

// hooks
import { useCustomState } from "shared/hooks/useCustomState";

// services
import { login, isPremium, } from "shared/services/api";

// contexts
import { useProxy } from "shared/contexts/ProxysContext";
import { useToast } from '@chakra-ui/react';

// constant
import * as Regex from 'shared/constants/regex';


const Login = () => {

    const { fetchProxys } = useProxy();
    const toast = useToast();

    const [loading, setLoading] = useState(false)

    const [loginInfo, setLoginInfo] = useCustomState({
        email: "",
        password: "",
        error: false,
        message: ""
    });


    const handleRegister = () => {
        window.open('https://sandvpn.com/register?source=firefox', '_blank');
        window.close();
    };

    const handleForgot = () => {
        window.open('https://sandvpn.com/forgot', '_blank');
        window.close();
    };

    const handleLogin = async () => {
        try {

            const email = loginInfo.email.trim();

            if (Regex.email.test(email)) {
                if (email && loginInfo.password.trim()) {
                    setLoading(true);

                    const res = await login({
                        email: loginInfo.email.trim().toLowerCase(),
                        password: loginInfo.password
                    });

                    if ("error" in res) {
                        setLoginInfo({
                            error: true,
                            message: res["error"]
                        });
                        setLoading(false);
                    }

                    else {

                        const premiumInfo = await isPremium(res.token);

                        if ('premium' in premiumInfo) {
                            if (premiumInfo.premium) {
                                await fetchProxys(res.token);
                            };

                            const userInfoDetail = { ...res, ...premiumInfo, isAuth: true }

                            browser.storage.sync.set({ userInfo: userInfoDetail });

                            toast({
                                description: "You are Connected",
                                status: 'success',
                                duration: 1500,
                                isClosable: true,
                            });
                        };
                    };
                }
                else {
                    setLoginInfo({
                        error: true,
                        message: "Fill in all fields"
                    });
                };
            }
            else {
                setLoginInfo({
                    error: true,
                    message: 'Please enter a valid email'
                });
            }

        } catch (e) {
            setLoading(false);
        };
    };

    const onKeyPress_ = (ev) => {
        if (ev.key === 'Enter') {
            handleLogin();
            ev.preventDefault();
        };
    };

    return (
        <Flex
            flex={1}
            align='center'
        >
            <Container
                maxW="lg"
                py={{ base: '12', md: '10' }}
                px={{ base: '0', sm: '8' }}
            >

                <Stack
                    spacing="8"
                    borderWidth={{ base: 'none', sm: 1 }}
                    borderRadius={5}
                >
                    <Box
                        py={{ base: '0', sm: '8' }}
                        px={{ base: '4', sm: '10' }}
                    >
                        <Stack textAlign="center" pb="6">
                            <Heading
                                color={'white'}
                                size={"lg"}
                            >
                                Login
                            </Heading>
                        </Stack>

                        <Stack spacing="6">
                            <Stack spacing="5">
                                <FormControl>
                                    <FormLabel
                                        htmlFor="email"
                                        color={'white'}
                                    >
                                        Email
                                    </FormLabel>
                                    <Input
                                        id="email"
                                        type="email"
                                        color={'white'}
                                        onChange={(event) => setLoginInfo({ email: event.target.value })}
                                        onKeyPress={onKeyPress_}
                                    />
                                </FormControl>
                                <PasswordField
                                    title={"Password"}
                                    onChange={(event) => setLoginInfo({ password: event.target.value })}
                                    onKeyPress={onKeyPress_}
                                />
                            </Stack>
                            <HStack justify="flex-end">
                                <Button
                                    variant="link"
                                    color={'gray.100'}
                                    size="sm"
                                    onClick={handleForgot}
                                >
                                    forgot?
                                </Button>
                            </HStack>
                            <Stack>
                                <Button colorScheme="blue" onClick={handleLogin} isLoading={loading}>
                                    Login
                                </Button>
                            </Stack>

                            {
                                loginInfo.error &&
                                <Stack textAlign="center" pos={"relative"}>
                                    <Text fontSize='16px' color='red.200'>
                                        {loginInfo.message}
                                    </Text>
                                </Stack>
                            }

                            <HStack spacing="2" justify="center">
                                <Text
                                    color={'gray.200'}
                                    fontSize={16}
                                >
                                    Don't have an account?
                                </Text>
                                <Button
                                    variant="link"
                                    color={'white'}
                                    onClick={handleRegister}
                                >
                                    Register
                                </Button>
                            </HStack>

                        </Stack>

                    </Box>
                </Stack>

            </Container>

        </Flex>
    );
};

export default Login;