/*global browser*/
import React, { useMemo, useCallback, useEffect } from 'react';

// hooks
import { useCustomState } from "shared/hooks/useCustomState";


const initialStateAuth = {
    token: "",
    refresh_token: "",
    email: "",
    registered_on: "",
    create_time: "",
    expiration: "",
    premium: false,
    isAuth: false
};

export const AuthContext = React.createContext();

export const AuthProvider = (props) => {

    const [userInfo, setUserInfo] = useCustomState(initialStateAuth);

    useEffect(() => {
        const initAuthStorage = async () => {
            const userInfoStorage = (await browser.storage.sync.get("userInfo")).userInfo;
            userInfoStorage ?
                setUserInfo(userInfoStorage) : browser.storage.sync.set({ userInfo: initialStateAuth });
        };

        initAuthStorage();
    }, []);


    const setStorageLogin = (dict) => {
        browser.storage.sync.set({ userInfo: dict });
    };

    const logout = useCallback(() => {
        setStorageLogin(initialStateAuth);
    }, []);

    const defaultAuth = useMemo(() => ({
        userInfo,
        setUserInfo,
        setStorageLogin,
        logout
    }), [userInfo]);

    return (
        <AuthContext.Provider value={defaultAuth}>
            {props.children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    const context = React.useContext(AuthContext);
    if (!context) {
        throw new Error('Error');
    }
    return context;
};






