import React from "react";

import {
    Flex,
    Text,
    Box,
    Image,
    HStack,
    VStack,
    Link,
    IconButton,
} from "@chakra-ui/react";

// icons
import { ChevronLeftIcon, EmailIcon } from '@chakra-ui/icons';


const Help = (props) => {
    return (
        <Flex
            flex={1}
            flexDir={'column'}
        >

            <Flex
                justify={'center'}
                align={'center'}
                h={'70px'}
                mb={'20px'}
            >
                <Box
                    pos={'absolute'}
                    left={'10px'}
                    cursor={'pointer'}
                    onClick={() => props.setSettings('menu')}
                >
                    <ChevronLeftIcon
                        color={'white'}
                        w={'40px'}
                        h={'40px'}
                    />
                </Box>
                <Text
                    color={'white'}
                    fontSize={24}
                >
                    Help
                </Text>
            </Flex>

            <Flex
                justify={'center'}
            >
                <Image
                    src={'/logo192.png'}
                    draggable={false}
                    width={'100px'}
                />
            </Flex>

            
            <VStack>
                <HStack
                    justify={'center'}
                    alignSelf={'center'}
                    mt={'50px'}
                >
                    <EmailIcon
                        color={'blue.200'}
                        w={'30px'}
                        h={'30px'}
                    />
                    <Box fontSize={16}>sandvpn@sandvpn.com</Box>
                </HStack>
                <IconButton
                    as={Link}
                    isExternal
                    href={'https://t.me/balthSandVPN'}
                    size="lg"
                    isRound={true}
                    _hover={{ bg: 'blue.800' }}
                    icon={<Image src={`/svg/telegram.svg`} w={'30px'} alt='' />}
                />
            </VStack>
        </Flex>
    );
};

export default Help;