import {
    Alert,
    AlertIcon,
    AlertTitle,
    AlertDescription,
} from '@chakra-ui/react';


export const FormAlert = ({
    title = '',
    description = '',
    alterProps = { borderRadius: 10, status: 'error', bg: 'blue.700' },
    alterTitleProps = {},
    alterDescriptionProps = { fontSize: 14, },
}) => {
    return (
        <Alert
            {...alterProps}
        >
            <AlertIcon />
            <AlertTitle {...alterTitleProps}>{title}</AlertTitle>
            <AlertDescription
                {...alterDescriptionProps}>
                {description}
            </AlertDescription>
        </Alert>
    );
};
