import React, { useEffect } from "react";

// components
import Root from "App/components/Root";

// contexts
import { useProxy } from "shared/contexts/ProxysContext";
import { useAuth } from "shared/contexts/Auth";
import { useManagement } from "shared/contexts/Management";

// services
import { isPremium, getNotifs, } from "shared/services/api";


function WrapperRoots() {

    const { setStorageLogin, setUserInfo, } = useAuth();
    const { fetchProxys, setProxy, setProxys, } = useProxy();
    const { setManagement, management } = useManagement();

    useEffect(() => {

        function logStorageChange(changes, area) {

            const changedItems = Object.keys(changes);

            for (const item of changedItems) {
                switch (item) {
                    case 'userInfo':
                        setUserInfo(changes[item].newValue);
                        break;
                    case 'info':
                        setProxy(changes[item].newValue);
                        break;
                    case 'proxyInfos':
                        setProxys(changes[item].newValue);
                        break;
                    case 'management':
                        setManagement(changes[item].newValue);
                        break;
                    default:
                        break;
                };
            };
        };

        const initUser = async () => {
            const userInfoStorage = (await browser.storage.sync.get("userInfo")).userInfo;
            const token = userInfoStorage?.token;
            if (token) {
                try {
                    const isUserPremium = (await isPremium(token))?.premium;
                    setStorageLogin({ ...userInfoStorage, premium: isUserPremium });
                } catch (e) {
                    setUserInfo(userInfoStorage);
                };
            };

            fetchProxys(token);
        };

        const initNotifs = async () => {
            const management_ = (await browser.storage.sync.get("management"))?.management;

            try {
                const notifs = await getNotifs();

                if (notifs?.length) {
                    const oldNotifs = management_?.notification;
                    let unread = notifs.filter(o1 => !oldNotifs?.read.some(o2 => o1.date === o2.date));
                    const newNotifs = { ...oldNotifs, all: notifs, unread }
                    const newManagement = { ...management, ...management_, notification: newNotifs, state: 'ready' };
                    browser.storage.sync.set({ management: newManagement });
                }
                else {
                    browser.storage.sync.set({ management: { ...management, ...management_, state: 'ready' } });
                };
            } catch (e) { browser.storage.sync.set({ management: { ...management, ...management_, state: 'ready' } }); };
        };

        browser.storage.onChanged.addListener(logStorageChange);
        initUser();
        initNotifs();

    }, []);

    return (
        <Root />
    );
}

export default WrapperRoots;

