const formEmail = {
    email: "",
};

const formPassword = {
    password: "",
};

const formLogin = {
    ...formEmail,
    ...formPassword,
};

const formRegister = {
    ...formLogin,
    confirm_password: "",
};

const formForgot = {
    ...formPassword,
    confirm_password: "",
};

const formChangePassword = {
    lastPassword: "",
    ...formPassword,
    confirm_password: "",
};

export const defaultForm = {
    login: formLogin,
    register: formRegister,
    forgot: formForgot,
    email: formEmail,
    password: formPassword,
    changePassword: formChangePassword,
};

