/*global browser*/
import React, { useEffect, useMemo, useState } from "react";

// hooks
import { useCustomState } from "shared/hooks/useCustomState";

// services
import { freeProxys, fetchAuthProxys, getUserIP } from "shared/services/api";
import { getRandomInt } from "shared/services/maths";

// constants
import { countryCode } from "shared/constants/countryCode";


const initialStateProxy = {
    isActive: false,
    host: "",
    country: "",
    port: "",
    city: "",
    username: "",
    password: "",
};

let timer = null;

export const ProxysContext = React.createContext();

export function ProxysProvider(props) {

    const [proxy, setProxy] = useCustomState(initialStateProxy);
    const [proxys, setProxys] = useState([]);

    const setStorageProxys = (dict) => {
        browser.storage.sync.set({ proxyInfos: dict });
    };

    const fetchProxys = async (token = null) => {
        let fetchedProxys;
        const proxyInfos = (await browser.storage.sync.get("proxyInfos")).proxyInfos;

        if (!token) {
            try {
                fetchedProxys = await freeProxys();
                setStorageProxys(fetchedProxys);
            } catch (e) {
                setProxys(proxyInfos);
            };
        }
        else {
            try {
                fetchedProxys = await fetchAuthProxys(token);
                setStorageProxys(fetchedProxys);
            } catch (e) {
                setProxys(proxyInfos);
            };
        };
    };

    const initProxy = async () => {
        let proxyInfo = (await browser.storage.sync.get("info"))?.info;

        if (proxyInfo?.isActive !== "start") {
            setProxy({ ...proxyInfo });
        };
    };

    const connectProxy = async (infos = {}) => {

        if (timer) clearTimeout(timer);

        // let userInfo = (await browser.storage.sync.get("userInfo"))?.userInfo;

        // const usageTimeRemaining_ = (await browser.storage.sync.get("usageTimeRemaining")).usageTimeRemaining;

        // if (userInfo?.premium || usageTimeRemaining_ > 0) {
            if (infos.username) {
                const { username, password, ...proxyInfo } = infos;
                const proxyDetails = { ...infos, isActive: 'connecting' };
                const auth = { username, password };

                browser.storage.sync.set({ info: proxyDetails, auth: auth });

                browser.runtime.sendMessage({
                    isActive: true,
                    code: countryCode[infos.country],
                    proxy: {
                        enabled: true,
                        type: 'http',
                        ...proxyDetails,
                        filters: { urls: ["<all_urls>"] }
                    }
                });

                timer = setTimeout(async () => {
                    try {
                        await getUserIP();
                        let proxyInfo = (await browser.storage.sync.get("info"))?.info;
                        if (proxyInfo.isActive === 'connecting') {
                            browser.storage.sync.set({ info: { ...proxyInfo, isActive: true } });
                        };
                        timer = null;
                    } catch (e) {
                        let proxyInfo = (await browser.storage.sync.get("info"))?.info;
                        if (proxyInfo.isActive === 'connecting') {
                            browser.storage.sync.set({ info: { ...proxyInfo, isActive: true } });
                        };
                        timer = null;
                    };
                }, 750);
            };
        // }
        // else {
        //     window.open('https://www.sandvpn.com/pricing', '_blank');
        //     window.close();
        // };
    };

    const connectRandomProxy = async () => {
        let userInfo = (await browser.storage.sync.get("userInfo"))?.userInfo;
        const availableProxys = proxys.filter(
            (info) =>
                info.paid === userInfo?.premium &&
                info.premium === userInfo?.isAuth
        );

        const randomInt = getRandomInt(availableProxys.length);
        connectProxy(availableProxys[randomInt]);
    };


    const disconnectProxy = () => {
        if (timer) clearTimeout(timer);
        browser.runtime.sendMessage({ isActive: false });
        browser.storage.sync.set({ info: { ...proxy, isActive: false } });
    }

    const handleConnect = () => {
        if (!proxy.isActive) {
            connectRandomProxy();
        }
        else disconnectProxy();
    };


    useEffect(() => {
        initProxy();
    }, []);

    const defaultProxys = useMemo(() => ({
        fetchProxys,
        proxys,
        proxy,
        setProxy,
        setProxys,
        connectProxy,
        handleConnect,
    }), [proxys, proxy]);

    return (
        <ProxysContext.Provider value={defaultProxys}>
            {props.children}
        </ProxysContext.Provider>
    );
};

export const useProxy = () => {
    const context = React.useContext(ProxysContext);
    if (!context) {
        throw new Error('Error');
    }
    return context;
};