export const fetchWithTimeout = (url, options = {}) => {
    const { timeout = 2500, ...fetchOptions } = options

    return Promise.race([
        fetch(url, fetchOptions),
        new Promise((_, reject) => {
            setTimeout(() => {
                reject(
                    new Error(
                        `Request for ${url} timed out after ${timeout} milliseconds`,
                    ),
                )
            }, timeout)
        }),
    ])
}