import React from "react";

// Chakra imports
import {
    Box,
} from "@chakra-ui/react";


// components
import { FormAlert } from "../components";


export default function AlertContainer({
    message,
    errorServer,
    loading,
    alertContainerProps = { w: '100%' },
}) {
    return (
        <Box
            display={loading ? 'none' : 'flex'}
            {...alertContainerProps}
        >
            {
                ((message || errorServer) && !loading) && (
                    <FormAlert
                        description={message ? message : errorServer}
                    />
                )
            }
        </Box>
    );
};

