import React from "react";

import {
    Flex,
    Text,
    Box,
    Image
} from "@chakra-ui/react";

// icons
import { ChevronLeftIcon } from '@chakra-ui/icons';

const links = [
    {
        title: "Our Website",
        href: "https://www.sandvpn.com/"
    },
    {
        title: "Privacy Policy",
        href: "https://www.sandvpn.com/policy"
    },
    {
        title: "Terms & Conditions",
        href: "https://www.sandvpn.com/terms"
    }
];

const AboutUs = (props) => {

    const handleOpenLink = (link) => {
        window.open(link, '_blank');
        window.close();
    };


    return (
        <Flex
            flex={1}
            flexDir={'column'}
        >

            <Flex
                justify={'center'}
                align={'center'}
                h={'70px'}
                mb={'20px'}
            >
                <Box
                    pos={'absolute'}
                    left={'10px'}
                    cursor={'pointer'}
                    onClick={() => props.setSettings('menu')}
                >
                    <ChevronLeftIcon
                        color={'white'}
                        w={'40px'}
                        h={'40px'}
                    />
                </Box>
                <Text
                    color={'white'}
                    fontSize={24}
                >
                    About Us
                </Text>

            </Flex>

            <Flex
                justify={'center'}
            >
                <Image
                    src={'/logo192.png'}
                    draggable={false}
                    width={'100px'}
                />
            </Flex>

            <Flex
                flexDir={'column'}
                align={'center'}
            >
                {
                    links.map((link, index) => (
                        <Flex
                            key={index}
                            align={'center'}
                            w={"90vw"}
                            p={"10px"}
                            borderRadius={5}
                            cursor={'pointer'}
                            border={'1px solid transparent'}
                            _hover={{
                                borderWidth: 1,
                                borderStyle: 'solid',
                                borderColor: "blue.300",
                                bg: 'rgba(67, 120, 187, .4)'
                            }}
                            onClick={() => handleOpenLink(link.href)}
                        >
                            <Text
                                color={'white'}
                                fontSize={16}
                            >
                                {link.title}
                            </Text>
                        </Flex>
                    ))
                }
            </Flex>
        </Flex>
    );
};

export default React.memo(AboutUs);