import React from "react";

import {
    Flex,
    Box,
    Image,
    Text,
    Divider,
    VStack,
    Spinner,
    useDisclosure,
    HStack,
    Tag,
    SlideFade,
} from "@chakra-ui/react";

import { useNavigate, } from 'react-router-dom';

import { motion } from "framer-motion";
import { chakra, shouldForwardProp } from '@chakra-ui/react';
import { isValidMotionProp } from 'framer-motion';

// contexts
import { useProxy } from "shared/contexts/ProxysContext";
import { useAuth } from "shared/contexts/Auth";

// components
import Notifs from "App/components/Notifs";
import { LinkCustom } from "shared/components/LinkCustom";
import { AndroidModal } from "shared/components/Dialog/AndroidModal";
import UpgradeCard from "App/components/Cards/UpgradeConnect";

// utils
import { openFeature, openUrl } from "shared/utils/tab";

// icons
import { ChromeIcon } from "shared/components/Icons/Chrome";


const connectContainerWIDTH = 160;

const ChakraBox = chakra(motion.div, {
    /**
     * Allow motion props and non-Chakra props to be forwarded.
     */
    shouldForwardProp: (prop) => isValidMotionProp(prop) || shouldForwardProp(prop),
});

const Connect = () => {

    const {
        proxy,
        proxys,
        handleConnect,
    } = useProxy();

    const { userInfo } = useAuth();
    const navigate = useNavigate();

    const {
        isOpen: isOpenNotif,
        onToggle: onToggleNotif,
        onClose: onCloseNotif
    } = useDisclosure();

    const handleGoPremium = () => {
        navigate('/payment');
    };

    return (
        <Flex
            flex={1}
            flexDir={'column'}
            minHeight='100vh'
            bgGradient='linear(#001F48 0%, blue.900 5%, blue.400 80%)'
        >

            <Flex
                justify={'space-between'}
                align={'center'}
                h={70}
                px={'20px'}
            >
                <HStack spacing={0}>
                    <Image
                        src={'/logo192.png'}
                        draggable={false}
                        width={45}
                    />
                    <Text
                        fontSize={24}
                        color={'white'}
                        fontFamily={'myFont1'}
                    >
                        SandVPN
                    </Text>
                </HStack>

                <HStack justify={'end'}>
                    <Tag colorScheme={userInfo.premium ? 'yellow' : 'red'} fontSize={16}>
                        {userInfo.premium ? 'PREMIUM' : 'FREE'}
                    </Tag>

                    <Notifs
                        isOpen={isOpenNotif}
                        onToggle={onToggleNotif}
                        onClose={onCloseNotif}
                    />

                </HStack>
            </Flex>

            <Flex
                flexDir={'column'}
                justify={'center'}
                align={'center'}
                mt={'7.5px'}
            >
                <Box
                    pos={'relative'}
                    width={connectContainerWIDTH}
                    h={connectContainerWIDTH}
                    alignSelf={'center'}
                >
                    {
                        proxy.isActive !== 'connecting' && (proxys?.length || (proxy.isActive))
                            ? (
                                <>
                                    {
                                        proxy.isActive ?
                                            <Box
                                                pos={'absolute'}
                                            >
                                                <ChakraBox
                                                    initial={{ opacity: 0 }}
                                                    animate={{
                                                        scale: [1, 1.2, 1.2, 1, 1],
                                                        opacity: [.2, 1, 1, .2, .2]
                                                    }}
                                                    transition={{
                                                        duration: 3,
                                                        ease: "easeInOut",
                                                        repeat: Infinity,
                                                        repeatType: "loop",
                                                    }}
                                                    border={'5px solid'}
                                                    borderColor='red.100'
                                                    borderRadius={'50%'}
                                                    w={connectContainerWIDTH}
                                                    h={connectContainerWIDTH}
                                                    pos={'absolute'}
                                                    top={"50%"}
                                                    left={"50%"}
                                                    transform={"translate(-50%, -50%)"}
                                                />

                                                <ChakraBox
                                                    initial={{ opacity: 0 }}
                                                    animate={{
                                                        scale: [1.2, 1, 1, 1.2, 1.2],
                                                        opacity: [1, .2, .2, 1, 1]
                                                    }}
                                                    transition={{
                                                        duration: 3,
                                                        ease: "easeInOut",
                                                        repeat: Infinity,
                                                        repeatType: "loop",
                                                    }}
                                                    border={'5px solid'}
                                                    borderColor='red.200'
                                                    borderRadius={'50%'}
                                                    w={connectContainerWIDTH}
                                                    h={connectContainerWIDTH}
                                                    pos={'absolute'}
                                                    top={"50%"}
                                                    left={"50%"}
                                                    transform={"translate(-50%, -50%)"}
                                                />
                                            </Box>
                                            :
                                            <Box
                                                pos={'absolute'}
                                                top={"50%"}
                                                left={"50%"}
                                                transform={"translate(-50%, -50%)"}
                                            >
                                                <Flex
                                                    align={'center'}
                                                    justify={'center'}
                                                    w={connectContainerWIDTH}
                                                    h={connectContainerWIDTH}
                                                    borderRadius={100}
                                                    borderStyle={'solid'}
                                                    borderWidth={5}
                                                    borderColor={"red.300"}
                                                />
                                            </Box>
                                    }
                                </>

                            )
                            :
                            (
                                <Spinner
                                    w={`${connectContainerWIDTH}px`}
                                    h={`${connectContainerWIDTH}px`}
                                    color={'red.300'}
                                    borderWidth={'5px'}
                                />
                            )
                    }
                    <Box
                        pos={'absolute'}
                        top={"50%"}
                        left={"50%"}
                        transform={"translate(-50%, -50%)"}
                    >
                        <Flex
                            align={'center'}
                            justify={'center'}
                            textTransform={'uppercase'}
                            width={connectContainerWIDTH}
                            h={connectContainerWIDTH}
                            cursor='pointer'
                            borderRadius={100}
                            fontSize={'18px'}
                            fontWeight={600}
                            color={"red.300"}
                            _hover={{
                                bg: "rgba(44, 82, 130, .5)",
                                opacity: .9
                            }}
                            onClick={handleConnect}
                            userSelect={'none'}
                        >
                            {proxy.isActive === 'connecting' ? 'CONNECTING' : proxy.isActive ? 'DISCONNECT' : !proxys?.length ? '' : 'CONNECT'}
                        </Flex>
                    </Box>
                </Box>

                {
                    proxy.isActive && (
                        <Flex
                            justify={'center'}
                            align={'center'}
                            mt={'25px'}
                        >
                            <VStack align={'center'}>
                                <HStack>
                                    <ChromeIcon
                                        h={'35.5px'}
                                        w={'36px'}
                                        cursor={'pointer'}
                                        onClick={() => openUrl({ url: 'https://chrome.google.com/webstore/detail/sandvpn-free-fast-unlimit/ngigoeagdgecjjbmielklflkmnlnddop', logText: 'open Chrome App' })}
                                    />
                                    <AndroidModal />
                                </HStack>

                                <Flex
                                    bgGradient='linear(blue.400, blue.600)'
                                    borderColor="blue.300"
                                    borderStyle="solid"
                                    borderWidth={1}
                                    flexDir="column"
                                    justify={"center"}
                                    align={'center'}
                                    textAlign={'center'}
                                    w='85vw'
                                    pt={15}
                                    pb={15}
                                    pr={25}
                                    pl={25}
                                    borderRadius={5}
                                    shadow={'lg'}
                                >
                                    <Flex
                                        justify={"center"}
                                        align={'center'}
                                    >
                                        <Image
                                            src={`${process.env.REACT_APP_API_URL}/static/flags/${proxy.country}R.png`}
                                            fallbackSrc={`/flags/${proxy.country}R.png`}
                                            h={'64px'}
                                            w={'64px'}
                                            borderRadius={25}
                                            mr={2}
                                        />

                                        <Flex
                                            align={'flex-start'}
                                            flexDir={'column'}
                                        >
                                            <Text
                                                fontSize={16}
                                                fontWeight="bold"
                                                color="white"
                                            >
                                                {proxy.country}
                                            </Text>

                                            <Text
                                                fontSize={14}
                                                color="white"
                                            >
                                                {proxy.city}
                                            </Text>
                                        </Flex>
                                    </Flex>

                                    <Divider color={'blue.300'} />

                                    <Box
                                        w="100%"
                                        color="white"
                                        fontSize={16}
                                        mt={2.5}
                                    >
                                        <Text>
                                            New IP:
                                            <Text as={'span'} pl={'5px'} fontSize={14}>
                                                {proxy.host}
                                            </Text>
                                        </Text>
                                    </Box>

                                </Flex>
                            </VStack>
                        </Flex>
                    )
                }
            </Flex>

            {
                proxy.isActive ? (
                    <VStack mt={'20px'} w={'100vw'} px={'10px'} align={'center'}>
                        {
                            !userInfo.premium && (
                                <SlideFade in={true} offsetX='-50px'>
                                    <HStack spacing={'2px'} userSelect={'none'}>
                                        <motion.div
                                            whileHover={{ scale: 1.1 }}
                                            whileTap={{ scale: 0.95 }}
                                        >
                                            <Box
                                                cursor={'pointer'}
                                                onClick={handleGoPremium}
                                                _hover={{ bgGradient: "linear(to-r, blue.300, red.300, red.300, blue.300)" }}
                                                borderRadius={50}>
                                                <Image
                                                    h={'50px'}
                                                    w={'50px'}
                                                    borderRadius={20}
                                                    src={`${process.env.REACT_APP_API_URL}/static/flags/JapanR.png`}
                                                    fallbackSrc='/flags/JapanR.png'
                                                />
                                            </Box>
                                        </motion.div>
                                        <motion.div
                                            whileHover={{ scale: 1.1 }}
                                            whileTap={{ scale: 0.95 }}
                                        >
                                            <Box
                                                cursor={'pointer'}
                                                onClick={handleGoPremium}
                                                _hover={{ bgGradient: "linear(to-r, blue.300, red.300, red.300, blue.300)" }}
                                                borderRadius={50}>
                                                <Image
                                                    h={'50px'}
                                                    w={'50px'}
                                                    borderRadius={20}
                                                    src={`${process.env.REACT_APP_API_URL}/static/flags/BelgiumR.png`}
                                                    fallbackSrc='/flags/BelgiumR.png'
                                                />
                                            </Box>
                                        </motion.div>
                                        <motion.div
                                            whileHover={{ scale: 1.1 }}
                                            whileTap={{ scale: 0.95 }}
                                        >
                                            <Box
                                                cursor={'pointer'}
                                                onClick={handleGoPremium}
                                                _hover={{ bgGradient: "linear(to-r, blue.300, red.300, red.300, blue.300)" }}
                                                borderRadius={50}>
                                                <Image
                                                    h={'50px'}
                                                    w={'50px'}
                                                    borderRadius={20}
                                                    src={`${process.env.REACT_APP_API_URL}/static/flags/UKR.png`}
                                                    fallbackSrc='/flags/UKR.png'
                                                />
                                            </Box>
                                        </motion.div>
                                        <HStack pl={'7.5px'}>
                                            <Image
                                                src={'/svg/premium.svg'}
                                                draggable={false}
                                                width={'42.5px'}
                                            />
                                            <Tag fontSize={16} fontWeight={700} color={'blue.800'} bg={'yellow.100'}>
                                                100x Faster
                                            </Tag>
                                        </HStack>
                                    </HStack>
                                </SlideFade>
                            )
                        }
                    </VStack>
                )
                    :
                    (
                        <VStack mt={'7.5px'} w={'100vw'} px={'10px'} align={'center'}>
                            <HStack py='3.5px'>
                                <ChromeIcon
                                    h={'35.5px'}
                                    w={'36px'}
                                    cursor={'pointer'}
                                    onClick={() => openUrl({ url: 'https://chrome.google.com/webstore/detail/sandvpn-free-fast-unlimit/ngigoeagdgecjjbmielklflkmnlnddop', logText: 'open Chrome App' })}
                                />
                                <AndroidModal />
                            </HStack>

                            {
                                !userInfo.premium && (
                                    <UpgradeCard />
                                )
                            }
                        </VStack>
                    )
            }
        </Flex>
    );
}

export default React.memo(Connect);