import React from "react";

import { HashRouter } from "react-router-dom";

// components
import WrapperRoots from "./WrapperRoots";

// contexts
import { ChakraProvider } from '@chakra-ui/react';
import { ProxysProvider } from "shared/contexts/ProxysContext";
import { AuthProvider } from "shared/contexts/Auth";
import { ManagementProvider } from "shared/contexts/Management";

// styles
import theme from "./theme";


function App() {
    return (
        <HashRouter>
            <ChakraProvider theme={theme}>
                <ProxysProvider>
                    <AuthProvider>
                        <ManagementProvider>
                            <WrapperRoots />
                        </ManagementProvider>
                    </AuthProvider>
                </ProxysProvider>
            </ChakraProvider>
        </HashRouter>
    );
};

export default App;

