import React from "react";

import {
    Flex,
    Text,
    Box,
    VStack
} from "@chakra-ui/react";

// components
import Privacy from "../components/Privacy";


const Menu = (props) => {

    const handleOpenLink = (link) => {
        window.open(link, '_blank');
        window.close();
    };

    return (
        <Flex
            flex={1}
            flexDir={'column'}
        >
            <Flex
                justify={'center'}
                align={'center'}
                h={'70px'}
            >
                <Text
                    color={'white'}
                    fontSize={24}
                >
                    Settings
                </Text>

            </Flex>

            <VStack
                align={'center'}
                spacing={'2.5px'}
            >

                <Flex
                    align={'center'}
                    w={"90vw"}
                    p={"5px"}
                    borderRadius={5}
                    cursor={'pointer'}
                    border={'1px solid transparent'}
                    _hover={{
                        borderWidth: 1,
                        borderStyle: 'solid',
                        borderColor: "rgba(67, 120, 187, 1)",
                        bg: 'rgba(67, 120, 187, .2)'
                    }}
                    onClick={() => handleOpenLink("https://www.sandvpn.com/help/faq")}
                >
                    <Text
                        color={'white'}
                        fontSize={16}
                    >
                        FAQ
                    </Text>
                </Flex>

                <Flex
                    align={'center'}
                    w={"90vw"}
                    p={"5px"}
                    borderRadius={5}
                    cursor={'pointer'}
                    border={'1px solid transparent'}
                    _hover={{
                        borderWidth: 1,
                        borderStyle: 'solid',
                        borderColor: "rgba(67, 120, 187, 1)",
                        bg: 'rgba(67, 120, 187, .2)'
                    }}
                    onClick={() => props.setSettings('help')}
                >
                    <Text
                        color={'white'}
                        fontSize={16}
                    >
                        Help
                    </Text>
                </Flex>

                <Flex
                    align={'center'}
                    w={"90vw"}
                    p={"5px"}
                    borderRadius={5}
                    cursor={'pointer'}
                    border={'1px solid transparent'}
                    _hover={{
                        borderWidth: 1,
                        borderStyle: 'solid',
                        borderColor: "rgba(67, 120, 187, 1)",
                        bg: 'rgba(67, 120, 187, .2)'
                    }}
                    onClick={() => props.setSettings('about')}
                >
                    <Text
                        color={'white'}
                        fontSize={16}
                    >
                        About Us
                    </Text>
                </Flex>
            </VStack>

            <VStack
                p='7.5px'
            >
                <Privacy />
            </VStack>
        </Flex>
    );
};

export default React.memo(Menu);