import React from 'react';

import {
    Box,
    HStack,
    Text,
    Flex,
    VStack,
    Image,
    Alert,
    AlertTitle,
    SlideFade,
} from '@chakra-ui/react';

// icons
import { UnlockIcon } from '@chakra-ui/icons';
import { InfinityIcon } from 'shared/components/Icons/Infinity';


export default function UpgradeCard() {

    const handleGoPremium = () => {
        window.open('https://www.sandvpn.com/pricing?source=firefox&page=connect', '_blank');
        window.close();
    };

    return (
        <Alert
            colorScheme='blue'
            // bg={'blue.600'}
            // bgGradient='linear(blue.700, blue.800, blue.800, blue.700)'
            bgGradient='linear(blue.800, blue.700, blue.600, blue.700, blue.800)'
            variant='subtle'
            flexDirection='column'
            alignItems='center'
            justifyContent='center'
            textAlign='center'
            shadow={'lg'}
            w={'100%'}
            borderRadius={5}
            borderWidth={1}
            borderColor={'blue.100'}
        >
            <AlertTitle fontSize={24} fontFamily={'myFont1'} color={'red.50'}>SandVPN Ultimate</AlertTitle>
            <HStack justify={'center'} mt={'15px'} mb={'7.5px'} spacing={'15px'}>
                <Box>
                    <Image
                        src='/images/Incognito.png'
                        w={'77.5px'}
                    />
                </Box>
                <VStack align={'start'}>
                    <HStack>
                        <Flex align={'center'}>
                            <InfinityIcon mr={'5px'} color={'yellow.100'} w='30px' h='30px' />
                            <Text fontWeight={600} fontSize={18}>Unlimited Time</Text>
                        </Flex>
                    </HStack>
                    <HStack>
                        <Flex align={'center'}>
                            <UnlockIcon mr={'7.25px'} color={'yellow.100'} w='27.5px' h='27.5px' />
                            <Text fontWeight={600} fontSize={18}>All Locations</Text>
                        </Flex>
                    </HStack>
                    <HStack>
                        <Flex align={'center'}>
                            <InfinityIcon mr={'5px'} color={'yellow.100'} w='30px' h='30px' />
                            <Text fontWeight={600} fontSize={18}>Unlimited Speed</Text>
                        </Flex>
                    </HStack>
                </VStack>
            </HStack>

            <Box
                mt={2.5}
                w={'full'}>
                <SlideFade in={true} offsetY='70px'>
                    <Flex
                        userSelect={'none'}
                        align={'center'}
                        justify={'center'}
                        width={'full'}
                        h={'40px'}
                        padding={2.5}
                        fontSize={16}
                        color={'white'}
                        cursor={'pointer'}
                        shadow={'rgba(0, 0, 0, 0.25) 0px 5px 15px'}
                        bg={'blue.500'}
                        borderRadius={5}
                        onClick={handleGoPremium}
                        _hover={{ bg: 'blue.400' }}
                    >
                        <Box
                            pr={'2.5px'}
                        >
                            <Image
                                src={'/svg/premium.svg'}
                                draggable={false}
                                width={'37.5px'}
                            />
                        </Box>
                        <Flex
                            flexDir={'column'}
                            align={'flex-start'}
                            pl={'2.5px'}
                        >
                            <Text
                                fontSize={22}
                                fontWeight={700}
                                color={'white'}
                            >
                                New Year Deal
                            </Text>
                        </Flex>
                    </Flex >
                </SlideFade>
            </Box>
        </Alert>
    );
};