/*global browser*/

// api 
import { setAnalytics } from 'shared/services/api';


export const openFeature = ({ logoText, path }) => {
    browser.tabs.query({}, async (tabs_) => {
        let extensionUrl = browser.runtime.getURL("index.html");
        const tab_ = tabs_.filter((tab) => {
            let baseUrl = tab.url.split('#')[0];
            return baseUrl === extensionUrl;
        });

        var updateProperties = { 'active': true };
        if (tab_.length) {
            await browser.tabs.update(tab_[0].id, updateProperties);
            try { await setAnalytics({ info: logoText }); } catch (e) { };
            window.close();
        }
        else {
            window.open(`${browser.runtime.getURL("index.html")}#/${path}`, '_blank');
            try { await setAnalytics({ info: logoText }); } catch (e) { };
            window.close();
        };
    });
};


export const openUrl = async ({ url, logText }) => {
    window.open(url, '_blank');
    try { await setAnalytics({ info: logText }); } catch (e) { };
    window.close();
};
