import React, { useEffect, useState, useRef, } from "react";

import {
    Flex,
    Text,
    VStack,
    Box,
    HStack,
    Image,
    Spinner,
    Step,
    StepDescription,
    StepIcon,
    StepIndicator,
    StepNumber,
    StepSeparator,
    StepStatus,
    StepTitle,
    Stepper,
    useSteps,
    Badge,
    Button,
    useDisclosure,
    useToast,
    Divider,
} from "@chakra-ui/react";

import { motion, isValidMotionProp } from 'framer-motion';
import { chakra, shouldForwardProp } from '@chakra-ui/react';

import { CheckCircleIcon, } from "@chakra-ui/icons";
import { RefundIcon } from "shared/components/Icons/Refund";

// api
import { getPlans, createPayPalSub, createCryptoCharge, } from "shared/services/api";

// constants
import { steps } from "./constants";

// contexts
import { useAuth } from "shared/contexts/Auth";

// components
import { CheckEmail } from "App/components/EmailInput";
import { LoadingModal } from "shared/components/LoadingModal";


const ChakraBox = chakra(motion.div, {
    /**
     * Allow motion props and non-Chakra props to be forwarded.
     */
    shouldForwardProp: (prop) => isValidMotionProp(prop) || shouldForwardProp(prop),
});

const Payment = () => {

    const { userInfo } = useAuth();
    const { isOpen, onOpen, onClose } = useDisclosure();
    const toast = useToast();
    const { activeStep, setActiveStep } = useSteps({
        index: 1,
        count: steps.length,
    });

    const [selectedPlan, setSelectedPlan] = useState('yearly');
    const [myPlan, setMyPlan] = useState({});
    const [plans, setPlans] = useState([]);
    const [loading, setLoading] = useState(true);
    const [disabledPayment, setDisabledPayment] = useState(userInfo?.email ? false : true);
    const [focusError, setFocusError] = useState(false);
    const userEmail = useRef(userInfo?.email ? userInfo.email : '');

    useEffect(() => {
        if (userInfo?.email) {
            setDisabledPayment(false);
        }
    }, [userInfo]);

    useEffect(() => {
        const fetchPlans = async () => {
            try {
                setLoading(true);
                const allPlans = await getPlans();
                setPlans(allPlans);
                setMyPlan(allPlans.filter(plan => plan.type === selectedPlan)[0])
                setLoading(false);
            } catch (e) { setLoading(false); }
        };

        fetchPlans();
    }, []);

    const handleSelectPlan = (plan) => {
        setSelectedPlan(plan.type);
        setMyPlan(plan);
    };

    const handleEmailValid = () => {
        setActiveStep(2);
        setDisabledPayment(false);
    };

    const handleEmailInvalid = () => {
        setActiveStep(1);
        setDisabledPayment(true);
    };

    const handlePay = async (payMethod) => {

        if (disabledPayment) {
            setFocusError(true);
            return;
        };

        if (payMethod === 'paypal') {
            onOpen();
            const subData = {
                email: userEmail.current,
                type: myPlan.type
            };
            try {
                const paymentLink = await createPayPalSub(subData);
                onClose();
                window.open(`${paymentLink}`, '_blank');
                window.close();
            } catch (e) {
                toast({
                    description: 'Something went wrong, Please contact us.',
                    status: 'error',
                    duration: 4000,
                    isClosable: true
                });
                onClose();
            };
        }
        else if (payMethod === 'card') {
            window.open(`${myPlan.stripe}?prefilled_email=${userEmail.current}`, '_blank');
            window.close();
        }
        else if (payMethod === 'crypto') {
            onOpen();
            try {
                const res = await createCryptoCharge({ email: userEmail.current, type: selectedPlan, });
                const hostedUrl = res['hosted_url'];
                onClose();
                window.open(hostedUrl, "_blank");
                window.close();
            } catch (res) {
                onClose();
                toast({
                    description: 'Something went wrong, Please contact us.',
                    status: 'error',
                    duration: 4000,
                    isClosable: true
                })
            }
        };
    };

    return (
        <Flex
            flex={1}
            flexDir={'column'}
        >
            {
                !userInfo?.email ? (
                    <Flex
                        w={'full'}
                        justify={'center'}
                    >
                        <Stepper index={activeStep} mt={'10px'} minW={'325px'}>
                            {steps.map((step, index) => (
                                <Step key={index}>
                                    <StepIndicator>
                                        <StepStatus
                                            complete={<StepIcon />}
                                            incomplete={<StepNumber />}
                                            active={<StepNumber />}
                                        />
                                    </StepIndicator>

                                    <Box flexShrink='0'>
                                        <StepTitle>{step.title}</StepTitle>
                                        <StepDescription color='gray.300'>{step.description}</StepDescription>
                                    </Box>

                                    <StepSeparator />
                                </Step>
                            ))}
                        </Stepper>
                    </Flex>
                )
                    :
                    (
                        <Flex
                            justify={'center'}
                            align={'center'}
                            h={70}
                            px={'20px'}
                            mb={'10px'}
                        >
                            <HStack spacing={0}>
                                <Image
                                    src={'/logo192.png'}
                                    draggable={false}
                                    width={45}
                                />
                                <Text
                                    fontSize={24}
                                    color={'white'}
                                    fontFamily={'myFont1'}
                                >
                                    SandVPN
                                </Text>
                            </HStack>
                        </Flex>
                    )
            }

            {
                loading && !plans.length && (
                    <Flex
                        flex={1}
                        justify={'center'}
                        align={'center'}
                    >
                        <Spinner
                            color='blue.100'
                            w={'50px'}
                            h={'50px'}
                        />
                    </Flex>
                )
            }

            <Flex
                flex={1}
                justify={'center'}
            >
                {
                    !loading && plans.length && (
                        <VStack align='center' px={'10px'} w={'100%'} mt={'17.5px'} mb={{ base: 0, md: '20px' }} maxW={'450px'}>
                            {plans.map((plan) => (
                                <ChakraBox
                                    display={'flex'}
                                    key={plan.type}
                                    bg={'blue.500'}
                                    border={'solid 2px'}
                                    borderColor={selectedPlan === plan.type ? 'red.200' : 'transparent'}
                                    borderRadius={10}
                                    h={'60px'}
                                    px={'10px'}
                                    w={'100%'}
                                    align={'center'}
                                    cursor={'pointer'}
                                    shadow={'lg'}
                                    onClick={() => handleSelectPlan(plan)}
                                    whileHover={{ scale: selectedPlan === plan.type ? 1 : 1.035 }}
                                    whileTap={{ scale: selectedPlan === plan.type ? 1 : 0.95 }}
                                    transition={{ duration: selectedPlan === plan.type ? 0 : 0.2 }}
                                >
                                    <Flex
                                        flex={1}
                                        w={'100%'}
                                        pos={'relative'}
                                    >
                                        {
                                            selectedPlan === plan.type && (
                                                <Box
                                                    pos={'absolute'}
                                                    right={0}
                                                    top={'-2.5px'}
                                                >
                                                    <CheckCircleIcon color={'red.200'} />
                                                </Box>
                                            )
                                        }
                                        <VStack spacing={0} align={'start'} minW={'125px'} justify={'center'}>
                                            <Text>{plan.title}</Text>
                                            {plan?.billed && (
                                                <Text
                                                    fontSize={10}
                                                    color={'gray.300'}
                                                >
                                                    {`Billed ${plan.billed}`}
                                                </Text>
                                            )}
                                        </VStack>

                                        <VStack spacing={0} align={'start'} justify={'center'} pr={'30px'}>
                                            <HStack justifyContent="end" spacing={0}>
                                                <Text fontSize={18} fontWeight="300" color="gray.300">
                                                    $
                                                </Text>
                                                <Text fontSize={22} fontWeight="600">
                                                    {plan.pricePerMonth}
                                                </Text>
                                                <Text fontSize={14} color="gray.300" mt={'auto'}>
                                                    /m
                                                </Text>
                                            </HStack>
                                        </VStack>

                                        <VStack justify={'center'}>
                                            <Badge
                                                bg='blue.100'
                                                shadow={'lg'}
                                                color={'blue.600'}
                                                fontSize={18}
                                                fontWeight={'bold'}>
                                                {plan.discount}
                                            </Badge>
                                        </VStack>
                                    </Flex>
                                </ChakraBox>
                            ))}

                            {
                                !userInfo?.email && (
                                    <Box pt={'2.5px'} w={'full'}>
                                        <CheckEmail
                                            email={userEmail}
                                            handleEmailValid={handleEmailValid}
                                            handleEmailInvalid={handleEmailInvalid}
                                            focusError={focusError}
                                            setFocusError={setFocusError}
                                        />
                                    </Box>
                                )
                            }

                            <HStack align={'center'} w={'full'} justify={'start'} pt={'1.5px'} spacing={'3.5px'}>
                                <RefundIcon
                                    h={'28.5px'}
                                    w={'27.5px'}
                                    color={'blue.100'}
                                />
                                <Text fontSize={12} fontWeight={700} pt={'3.5px'}>
                                    30-DAY MONEY-BACK GUARANTEE
                                </Text>
                            </HStack>

                            <VStack w={'full'} pt={'1.5px'}>
                                <Button
                                    w={'full'}
                                    bg={'blue.100'}
                                    color={'white'}
                                    _hover={{ bg: 'blue.200' }}
                                    shadow={'lg'}
                                    onClick={() => handlePay('paypal')}
                                >
                                    <Image
                                        src="/svg/paypal2.svg"
                                        h={'100px'}
                                        w={'100px'}
                                    />
                                </Button>

                                <Button
                                    w={'full'}
                                    bg={'blue.100'}
                                    color={'white'}
                                    _hover={{ bg: 'blue.200' }}
                                    shadow={'lg'}
                                    onClick={() => handlePay('card')}
                                >
                                    <HStack spacing={'2.5px'}>
                                        <Image
                                            src={`/svg/visa.svg`}
                                            width={50}
                                            height={50}
                                            alt={'visa payment'}
                                        />
                                        <Image
                                            src={`/svg/mastercard.svg`}
                                            width={50}
                                            height={50}
                                            alt={'mastercard payment'}
                                        />
                                    </HStack>
                                </Button>

                                <Button
                                    w={'full'}
                                    bg={'blue.100'}
                                    color={'white'}
                                    _hover={{ bg: 'blue.200' }}
                                    shadow={'lg'}
                                    onClick={() => handlePay('crypto')}
                                >
                                    <HStack spacing={'2px'}>
                                        <Image
                                            src={`/svg/bitcoin.svg`}
                                            width={'30px'}
                                            height={'30px'}
                                            alt={'Bitcoin BTC crypto payments'}
                                        />
                                        <Image
                                            src={`/svg/ltc.svg`}
                                            width={'30px'}
                                            height={'30px'}
                                            alt={'Ltc crypto payments'}
                                        />
                                        <Image
                                            src={`/svg/dogecoin.svg`}
                                            width={'30px'}
                                            height={'30px'}
                                            alt={'dogecoin crypto payments'}
                                        />
                                        <Image
                                            src={`/svg/ethereum.svg`}
                                            width={'30px'}
                                            height={'30px'}
                                            alt={'ethereum crypto payments'}
                                        />
                                    </HStack>
                                </Button>
                            </VStack>
                        </VStack>
                    )
                }
            </Flex>

            <LoadingModal
                isOpen={isOpen}
            />
        </Flex>
    );
};

export default Payment;