import React, { useState, useEffect } from "react"

import {
    Flex,
    Image,
    Text,
    Input,
    InputGroup,
    InputLeftElement,
    Spinner,
} from "@chakra-ui/react";

import { Search2Icon } from '@chakra-ui/icons';

import { useNavigate } from "react-router-dom";

// icons
import { ChevronRightIcon } from '@chakra-ui/icons';

// contexts
import { useProxy } from "shared/contexts/ProxysContext";
import { useAuth } from "shared/contexts/Auth";

// hooks
import { useCustomState } from "shared/hooks/useCustomState";


const Locations = () => {

    const navigate = useNavigate();

    const { connectProxy, proxys } = useProxy();
    const { userInfo } = useAuth();

    const [proxysList, setProxyList] = useState(proxys);

    const [search, setSearch] = useCustomState({ value: "" });


    useEffect(() => {
        if (proxysList?.length === 0) {
            setProxyList(proxys);
        }
    }, [proxys]);


    const handleSubmitSearch = (event) => {
        if (event.target.value) {
            const newData = proxys.filter(item => {
                const itemData = item.city.toUpperCase();
                const resSearch = event.target.value.toUpperCase();
                return itemData.indexOf(resSearch) > -1;
            });

            setProxyList(newData);
            setSearch({ value: event.target.value });
        }
        else {
            setProxyList(proxys);
            setSearch({ value: event.target.value });
        }
    };


    const connectPremiumProxy = async (itemProxy) => {
        if (userInfo.premium) {
            connectProxy(itemProxy);
            navigate(`/`);
        }
        else {
            navigate(`/payment`);
        };
    };

    const connect = async (itemProxy) => {
        if (itemProxy.paid) {
            connectPremiumProxy(itemProxy);
        }
        else {
            if (!itemProxy.premium || userInfo.isAuth) {
                connectProxy(itemProxy);
                navigate(`/`);
            }
            else navigate("/user");
        }
    };

    return (

        <Flex
            flex={1}
            flexDir={'column'}
            bgGradient='linear(#001F48 0%, blue.900 5%, blue.400 80%)'
            h={'calc(100vh - 60px)'}
        >

            <Flex
                justify={'center'}
                pt={5}
            >
                <InputGroup
                    w={'80vw'}
                >
                    <InputLeftElement
                        pointerEvents='none'
                        children={<Search2Icon color='gray.300' />}
                    />
                    <Input
                        placeholder='Search city...'
                        color={'white'}
                        bg={'blue.600'}
                        borderColor={'blue.400'}
                        _placeholder={{ color: 'gray.200' }}
                        onChange={handleSubmitSearch}
                    />
                </InputGroup>
            </Flex>

            <Flex
                h={'calc(70vh-10px)'}
                flex={1}
                flexDir={'column'}
                overflowY="auto"
                pos='relative'
                overflowX="hidden"
                mt={'10px'}
                pb={'20px'}
                align={'center'}
                css={{
                    '&::-webkit-scrollbar': {
                        width: '4px',
                    },
                    '&::-webkit-scrollbar-track': {
                        width: '6px',
                    },
                    '&::-webkit-scrollbar-thumb': {
                        background: '#90CDF4',
                        borderRadius: '24px',
                    },
                }}
            >
                {
                    proxysList && proxysList.length ?
                        proxysList.map((itemProxy, index) => (
                            <Flex
                                key={index}
                                flexDir={'column'}
                                w={"90vw"}
                                p={'10px'}
                                borderRadius={5}
                                cursor={'pointer'}
                                border={'1px solid transparent'}
                                _hover={{
                                    borderWidth: 1,
                                    borderStyle: 'solid',
                                    borderColor: 'blue.300',
                                    bg: 'rgba(67, 120, 187, .45)'
                                }}
                                onClick={() => connect(itemProxy)}
                            >
                                <Flex
                                    align='center'
                                >

                                    <Image
                                        src={`${process.env.REACT_APP_API_URL}/static/flags/${itemProxy.country}R.png`}
                                        h={'50px'}
                                        w={'50px'}
                                        borderRadius={20}
                                    />

                                    <Flex
                                        flexDir={"column"}
                                        alignItems={"flex-start"}
                                        pl={'7.5px'}
                                    >
                                        <Text
                                            color={"white"}
                                            fontSize={16}
                                            fontWeight={'bold'}
                                        >
                                            {itemProxy.country}
                                        </Text>
                                        <Text
                                            color={"white"}
                                            fontSize={14}
                                        >
                                            {itemProxy.city}
                                        </Text>
                                    </Flex>

                                    <Flex
                                        flex={1}
                                        justify={'flex-end'}
                                        align={'center'}
                                    >
                                        {
                                            itemProxy.paid ?
                                                <Flex
                                                    justify={'center'}
                                                    align={'center'}
                                                    p={'3.5px'}
                                                >
                                                    <Image
                                                        src={'/svg/premium.svg'}
                                                        draggable={false}
                                                        width={'35px'}
                                                    />
                                                </Flex>
                                                :
                                                itemProxy.premium &&
                                                !userInfo.isAuth &&
                                                <Text
                                                    color={'white'}
                                                    fontWeight={600}
                                                    bg={'blue.700'}
                                                    p={2}
                                                    alignSelf={'flex-start'}
                                                    rounded={'md'}
                                                    fontSize={14}
                                                >
                                                    Login to Unlock
                                                </Text>
                                        }

                                        <ChevronRightIcon
                                            color={'white'}
                                            w={'40px'}
                                            h={'40px'}
                                        />
                                    </Flex>
                                </Flex>

                            </Flex>
                        ))
                        :
                        search && search.value ?
                            <Flex
                                flexDir={'column'}
                                w={"90vw"}
                                p={'10px'}
                                borderRadius={5}
                                cursor={'pointer'}
                                border={'1px solid rgba(67, 120, 187, 1)'}
                                bg='rgba(67, 120, 187, .4)'
                            >
                                <Text
                                    color={"white"}
                                    fontSize={14}
                                >
                                    No Results...
                                </Text>
                            </Flex>
                            :
                            <Flex
                                flex={1}
                                align={'center'}
                                justify={'center'}
                            >
                                <Spinner size='xl' />
                            </Flex>
                }
            </Flex>

        </Flex >
    );
};

export default React.memo(Locations);