import React from "react";

import {
    Box,
    Divider,
    VStack,
    Popover,
    PopoverTrigger,
    PopoverContent,
    PopoverHeader,
    PopoverBody,
    PopoverArrow,
    PopoverCloseButton,
    Tabs,
    TabList,
    TabPanels,
    Tab,
    TabPanel
} from "@chakra-ui/react";

import { BellIcon } from "@chakra-ui/icons";

// contexts
import { useManagement } from "shared/contexts/Management";



const Notifs = ({ isOpen, onToggle, onClose }) => {

    const { management } = useManagement();
    const unreadNotifs = management?.notification?.unread;
    const allNotifs = management?.notification?.all;

    const handleClose = () => {
        if (unreadNotifs?.length) {
            const newManagement = {
                ...management,
                notification: {
                    unread: [],
                    read: [...unreadNotifs, ...allNotifs],
                    all: allNotifs,
                }
            }
            browser.storage.sync.set({ management: newManagement });
        };
        onClose();
    };

    return (
        <Popover
            isOpen={isOpen}
            onClose={handleClose}
        >
            <PopoverTrigger

            >
                <Box pos={'relative'}>
                    <BellIcon
                        color={'blue.300'}
                        w={'30px'}
                        h={'30px'}
                        cursor={'pointer'}
                        onClick={onToggle}
                    />
                    {
                        unreadNotifs?.length ? (
                            <Box
                                w={'12px'}
                                h={'12px'}
                                bg={'red.200'}
                                pos={'absolute'}
                                top={'4px'}
                                right={'2.5px'}
                                borderRadius={100}
                                border={'solid 3px'}
                                borderColor={'blue.900'}
                            />
                        )
                            :
                            (<Box />)
                    }
                </Box>
            </PopoverTrigger>
            <PopoverContent
                _focus={{ boxShadow: "none", }}
                bg={'blue.700'}
                color="white"
            >
                <PopoverArrow bg={'blue.700'} />
                <PopoverCloseButton />
                <PopoverHeader>Notifications</PopoverHeader>
                <PopoverBody
                    maxH={'300px'}
                    overflowY={'auto'}
                >
                    <Tabs variant='solid-rounded' colorScheme='blue' w={'100%'}>
                        <TabList mb='1em'>
                            <Tab color={'blue.50'} px={'10px'} mr={'7.5px'}>Unread</Tab>
                            <Tab color={'blue.50'} px={'10px'} >All</Tab>
                        </TabList>
                        <TabPanels w={'100%'}>
                            <TabPanel py={0}>
                                {
                                    unreadNotifs.length ? (
                                        <VStack>
                                            {
                                                unreadNotifs.map((notif, index) => (
                                                    <VStack
                                                        key={index}
                                                        w={'100%'}
                                                        align={'start'}
                                                    >
                                                        <VStack align={'start'}>
                                                            <Box>
                                                                {notif.text}
                                                            </Box>
                                                            <Box fontSize={14} color={'gray.200'}>
                                                                {notif.date}
                                                            </Box>
                                                            <Divider />
                                                        </VStack>
                                                    </VStack>
                                                ))
                                            }
                                        </VStack>
                                    )
                                        :
                                        (
                                            <VStack align={'start'}>
                                                <Box>
                                                    You&apos;re all up to date
                                                </Box>
                                                <Box fontSize={14} color={'gray.200'}>
                                                    There are no new notifications at the moment.
                                                </Box>
                                            </VStack>
                                        )
                                }
                            </TabPanel>
                            <TabPanel py={0} >
                                {
                                    allNotifs?.length ? (
                                        <VStack>
                                            {
                                                allNotifs.map((notif, index) => (
                                                    <VStack
                                                        key={index}
                                                        w={'100%'}
                                                        align={'start'}
                                                    >
                                                        <VStack align={'start'}>
                                                            <Box>
                                                                {notif.text}
                                                            </Box>
                                                            <Box fontSize={14} color={'gray.200'}>
                                                                {notif.date}
                                                            </Box>
                                                            <Divider />
                                                        </VStack>
                                                    </VStack>
                                                ))
                                            }
                                        </VStack>
                                    )
                                        :
                                        (
                                            <VStack align={'start'}>
                                                <Box>
                                                    You&apos;re all up to date
                                                </Box>
                                                <Box fontSize={14} color={'gray.200'}>
                                                    There are no new notifications at the moment.
                                                </Box>
                                            </VStack>
                                        )
                                }
                            </TabPanel>
                        </TabPanels>
                    </Tabs>
                </PopoverBody>
            </PopoverContent>
        </Popover>
    );
}

export default Notifs;