import * as React from 'react';
import {
    FormControl,
    FormLabel,
    FormHelperText,
    Input,
} from '@chakra-ui/react';

// utils
import { onEnterPress_ } from '../utils';


export const InputForm = ({
    error,
    errorProps,
    noErrorProps,
    formLabel,
    helperText,
    formLabelProps,
    formHelperTextProps,
    formControlProps,
    inputGroupProps,
    inputProps,
    onEnterPress,
    ...props
}) => {
    let isValidProps = error ? errorProps : noErrorProps;
    return (
        <FormControl {...formControlProps}>
            <FormLabel htmlFor="password" {...formLabelProps}>{formLabel}</FormLabel>
            <Input
                name="email"
                required
                onKeyPress={(ev) => onEnterPress_(ev, onEnterPress)}
                {...isValidProps}
                {...inputProps}
                {...props}
            />
            {
                helperText && (
                    <FormHelperText {...formHelperTextProps}>
                        {helperText}
                    </FormHelperText>
                )
            }
        </FormControl>
    )
};

InputForm.displayName = 'InputForm';

InputForm.defaultProps = {
    formLabel: 'Email',
    helperText: '',
    formLabelProps: { fontSize: 'sm', fontWeight: 'normal', },
    formControlProps: { isRequired: true, },
    inputGroupProps: { size: 'lg', },
    inputProps: {
        fontSize: 'sm',
        borderRadius: '15px',
        placeholder: 'Your email address',
        size: 'lg',
        _placeholder: { color: 'gray.300' },
        _hover: { borderColor: 'blue.100' }
    },
    error: false,
    errorProps: { borderColor: 'red.200', borderWidth: '2px' },
    noErrorProps: { borderColor: 'blue.200', borderWidth: '1px' },
    formHelperTextProps: {
        color: 'black'
    },
    onEnterPress: () => null,
};