/*global browser*/
import React, { useEffect } from 'react';

import {
    SimpleGrid,
    Flex,
    Switch,
    FormControl,
    FormLabel,
    Text
} from '@chakra-ui/react';


// hooks
import { useCustomState } from "shared/hooks/useCustomState";


export default function Privacy() {

    const [state, setState] = useCustomState({
        webrtc: false,
        tracking: false,
        fingerprinting: false,
        peerConnectionEnabled: true
    });

    const privacySettings = {
        webrtc: 'Block WebRTC IP Detection',
        tracking: 'Tracking Protection',
        fingerprinting: 'Fingerprinting Protection',
        peerConnectionEnabled: 'Peer Connection Enabled'
    };

    const handleChange = (event) => {
        setState({ [event.target.value]: event.target.checked });
        browser.storage.sync.set({ [event.target.value]: event.target.checked });
    };


    useEffect(() => {
        browser.storage.sync.get(['webrtc', 'tracking', 'fingerprinting', 'peerConnectionEnabled'],
            async function (res) {
                setState({
                    webrtc: res.webrtc ? true : false,
                    tracking: res.tracking ? true : false,
                    fingerprinting: res.fingerprinting ? true : false,
                    peerConnectionEnabled: res.peerConnectionEnabled ? true : false
                });
            }
        );
    }, [])


    return (
        <FormControl
            as={SimpleGrid}
            spacing={'10px'}
            columns={1}
            p={'7.5px'}
            borderRadius={5}
            bg={'blue.900'}
            shadow={'lg'}
        >
            <Text fontSize={16} fontWeight={600} mb='2.5px'>Proxy</Text>
            {
                Object.keys(privacySettings).map(key => (
                    <Flex
                        align={'center'}
                    >
                        <Switch
                            sx={{
                                'span.chakra-switch__track:not([data-checked])': { bg: 'gray.400' },
                                'span.chakra-switch__track[data-checked]': { bg: 'blue.300' },
                            }}
                            size='md'
                            value={key}
                            isChecked={state[key]}
                            onChange={handleChange}
                        />
                        <FormLabel
                            ml='5px'
                            mb='0'
                            textColor={'gray.200'}
                            fontSize={14}
                        >
                            {privacySettings[key]}
                        </FormLabel>
                    </Flex>
                ))
            }
        </FormControl>
    );
};